import {Form} from "react-bootstrap"
import{Row,Col,Container,Button} from "react-bootstrap"
import Navbarfn from "../Navbar/navbar"
import Navbar from "../Navbar/navbar"
import "./signin.css"
import {Link} from "react-router-dom"
import { useState } from "react"
import axios from "axios"
import { useNavigate } from "react-router-dom"
import { useParams } from "react-router-dom";
export default function NewPassword() {
  let navigate=useNavigate()
  const {token}=useParams()
  console.log("use params id in new password page is "+token);
const [login, setlogin] = useState({

  password:""

});
  const changeLogin=(e)=>{
        const {name,value}=e.target;
        setlogin({...login,[name]:value})
        
          }
          const addLogin=(e)=>{
            e.preventDefault()
          // setlogin({...login})
          console.log(" datas are"+login);
          axios.post(`/api/newpass/${token}`,login).then((response)=>{
            const datas=JSON.stringify(response)
            console.log("new password datas"+datas);
            console.log(" new password data added is "+JSON.stringify(response.data.detail));
            //looking for token that has been passed from server
            // console.log("response data token is"+response.data.token);
              navigate("/Signin")
            // let roles=response.data.detail.role
            // console.log("user role is"+roles);
            // localStorage.setItem("tokendata",response.data.token)
         
          })
          }
    return(
        <>
                <div id="container">
        <Container fluid> 
  <Row>
      {/* <div id="header">
   <img src="https://th.bing.com/th/id/OIP.T19yiW1DqxTw59kGQgT5AgAAAA?pid=ImgDet&rs=1" alt="" srcset="" />
   <span>DEC</span>
   </div> */}
   <Navbarfn/>
  </Row>
  <Row id="r1">

  </Row>
  <Row>
    <Col xs={4}></Col>
    <Col xs={4}>
    <Form id="form" onSubmit={addLogin}>
       
        <Form.Label  id="smalltext" size="sm" >Enter Your New Password</Form.Label>         
 
  <Form.Group className="mb-3" controlId="exampleForm.ControlPassword">
    
   
    <Form.Control type="password" placeholder="New Password" name="password" onChange={changeLogin} value={login.password}/>
    <br></br>
   <Button type="submit" variant="danger" className="mb-3" id="button" >SignIn</Button>{' '}

  

  </Form.Group>
</Form>
    </Col>
    <Col xs={4}></Col>
  </Row>
</Container>
       
        </div>
        </>
    )
}