import {Form} from "react-bootstrap"
import{Row,Col,Container,Button} from "react-bootstrap"
import Navbarfn from "../Navbar/navbar"
import Navbar from "../Navbar/navbar"
import {Link, Navigate} from "react-router-dom"
import "./signup.css"
import axios from "axios"
import { useState } from "react"
import { useNavigate } from "react-router-dom"
export default function Signup() {
  let Navigate=useNavigate()
  const [regin, setregin] = useState({
    uname:"",
    email:"",
    phone:"",
    place:"",
    role:"",
    username:"",
    password:""
  });
    const changeRegin=(e)=>{
          const {name,value}=e.target;
          setregin({...regin,[name]:value})
          
            }
            const addRegin=(e)=>{
              e.preventDefault()
            // setregin({...regin})
            // const demoobject={
            //   uname:regin.uname,
            //   email:regin.email,
            //   phone:regin.phone,
            //   place:regin.place,
            //   role:regin.role,
            //   username:regin.username,
            //   password:regin.password}
              console.log(" datas are"+regin);
            axios.post("/api/Signup",regin).then((data)=>{
              const datas=JSON.stringify(data)
              console.log("client side datas"+datas);
              console.log("data added is "+JSON.stringify(data.data.detail));

              if(data.data=="error"){
                alert("user with same email exist")
              }
              if(data.data=="select your role"){
                alert("you should select your role then only you can visit your dahshboard")
              }
              Navigate("/Signin")
            }).catch(()=>{
              alert("no data")
            })
            }
    
    return(
        <>
                <div id="container">
        <Container fluid> 
  <Row>
      {/* <div id="header">
   <img src="https://th.bing.com/th/id/OIP.T19yiW1DqxTw59kGQgT5AgAAAA?pid=ImgDet&rs=1" alt="" srcset="" />
   <span>DEC</span>
   </div> */}
   <Navbarfn/>
  </Row>
  <Row id="r1">

  </Row>
  <Row>
    <Col xs={4}></Col>
    <Col xs={4}>
    <Form id="form" onSubmit={addRegin}>
        <Form.Label id="userlogin" className="justify-content-center" size="lg">SIGNUP</Form.Label>  
        <br></br>
        {/* <Form.Label  id="smalltext" size="sm">Enter Username and Password to Login</Form.Label>          */}
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control1">
 
    <Form.Control type="text" placeholder="Name" name="uname"  onChange={changeRegin}  />
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control2">
 
    <Form.Control type="email" placeholder="Email"  name="email" onChange={changeRegin} />
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control3">
 
    <Form.Control type="number" placeholder="Mobile"  name="phone" onChange={changeRegin} />
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control4">
 
    <Form.Control type="text" placeholder="Place" name="place"  onChange={changeRegin} />
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control5">
  <Form.Select aria-label="Default select example" name="role" required onChange={changeRegin} >
  <option>Select Your Role </option>
  <option value="patient" name="patient" >Patient</option>
  {/* <option value="doctor" name="doctor">Doctor</option> */}
  <option value="admin" name="admin">Admin</option>
</Form.Select>
   
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control6">
 
    <Form.Control type="email" placeholder="Enter UserEmail" name="username"  required onChange={changeRegin} />
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlPassword">
    
   
    <Form.Control type="password" placeholder="Password"  name="password" required  onChange={changeRegin} />
    <br></br>
   {/* <Link to="/Signin"> */}
     <Button variant="danger" type="submit" className="mb-3" id="button" >SIGNUP</Button>{' '}
     {/* </Link> */}
  </Form.Group>
</Form>
    </Col>
    <Col xs={4}></Col>
  </Row>
</Container>
       
        </div>
        </>
    )
}