import "./about.css"
import {Container,Row,Navbar,Nav, Col,ListGroup} from "react-bootstrap"
import { Button } from "react-bootstrap"
import Navbarfn from "../Navbar/navbar"
import {Link} from "react-router-dom"
export default function About () {
    return(
        <>
        <div>
            <Container fluid >
            <Row>
    {/* <Col>1 of 1</Col> */}
    <Navbarfn/>
  </Row>
  <Row><h1 class="animat"> Welcome To Zoron Care Hospital</h1></Row>
      <Row id="about">
          <Col xs={8}>
      <p>  <b> Zoron Care Hospital</b>, Kollam, a part of Zoron Care Group of Hospitals, is one of the fastest growing multi-specialty hospital chains in India, managed by dedicated professionals, engaged in providing primary as well as tertiary healthcare services. Founded by Dr.Prathap Kumar,Zoron Care Hospital is the largest chain of hospitals in kollam today, with a pan India presence.</p>

 <p>TheZoron Care Group of Hospitals is known for its ethical medical practices, which makes it a preferred choice for patients. Moreover, while achieving clinical outcomes on par with international standards,Zoron Care Hospital make advanced procedures accessible at a fraction of the international costs.</p>

<p>Meditrina Hospital, Kollam, belongs to theZoron Care Group of Hospitals that aims to bring world class health care service within the reach of the Indian public. We serve the community with the help of highly qualified, nationally and internationally experienced doctors with Social commitment ensuring affordable cost of treatment.</p>

<p>Beginning a new age in the world of medical field, the initial Endeavour,Zoron Care Cardiac centre has already positioned in the history of Kerala in the opening year itself through the highest Number of Angioplasties performed. And it is functioning successfully in conjunction with a solid commitment for public welfare and no attention to monetary benefit.</p>
          </Col>
          <Col xs={4}>
          <ListGroup>
  <ListGroup.Item><b>Our Departments</b></ListGroup.Item>
  <ListGroup.Item action variant="primary">Cardiology</ListGroup.Item>
  <ListGroup.Item action variant="secondary">General Surgery</ListGroup.Item>
  <ListGroup.Item action variant="success">Nephrology</ListGroup.Item>
  <ListGroup.Item action variant="danger">Urology</ListGroup.Item>
  <ListGroup.Item action variant="warning">ENT</ListGroup.Item>
  <ListGroup.Item action variant="info">Pathology</ListGroup.Item>
  <ListGroup.Item action variant="light">Gynaecology</ListGroup.Item>
  <ListGroup.Item action variant="dark">neurology</ListGroup.Item>
</ListGroup>
          </Col>
      </Row>
            </Container>
            </div>
        </>
    )
}