import {Form} from "react-bootstrap"
import{Row,Col,Container,Button} from "react-bootstrap"
import Navbarfn from "../Navbar/navbar"
import Navbar from "../Navbar/navbar"
import "./signin.css"
import {Link} from "react-router-dom"
import { useState } from "react"
import axios from "axios"
import { useNavigate } from "react-router-dom"
import GoogleLogin from 'react-google-login';

export default function Signin () {
  let navigate=useNavigate()
const [login, setlogin] = useState({
  username:"",
  password:""

});
  const changeLogin=(e)=>{
        const {name,value}=e.target;
        setlogin({...login,[name]:value})
        
          }
          const addLogin=(e)=>{
            e.preventDefault()
          // setlogin({...login})
          console.log(" datas are"+JSON.stringify(login));
          axios.post("/api/Signin",login).then((response)=>{
            const datas=JSON.stringify(response)
            console.log("Login side datas"+datas);
            console.log("data added is "+JSON.stringify(response.data.detail));
            //looking for token that has been passed from server
            console.log("response data token is"+response.data.token);

            let roles=response.data.detail.role
            console.log("user role is"+roles);
            if(response.data.detail=="NO USER"){
              alert("User not found")
            }
           else if(response.data.detail=="PASSWORD INCORRECT"){
             alert("You Have Entered Wrong Password")
            }

            localStorage.setItem("tokendata",response.data.token)
            if(roles==="admin"){
              //store token in localstorage
              // localStorage.setItem("tokendata",response.data.token)
             navigate("/Adminhome")
            }
            else if(roles=="patient"){
              // localStorage.setItem("tokendata",response.data.token)
             navigate("/Patienthome")
            }
            else if(roles=="doctor"){
              // localStorage.setItem("tokendata",response.data.token)
            navigate("/Doctorhome")
            }
            // else{
            //   alert("some thing went wrong")
            // }
          })
          }
          const responseGoogle=(response)=>{
            console.log(response);
            axios({method:"POST",
          url:"/api/google-login",
          data:{tokenId:response.tokenId}
          }).then((responseData)=>{
            console.log(responseData.data);
            if(responseData.data=="no-user"){
              alert("no user with this email exiats...")
              navigate("/Signup")
            }
            else{
              if(responseData.data.details.role=="admin"){
                navigate("/Adminhome")
              }
              else if(responseData.data.details.role=="patient"){
                navigate("/Patienthome")
              }
              else if(responseData.data.details.role=="doctor"){
                navigate("/Doctorhome")
              }
              else{
               
                alert("Some thing went wrong")
               
              }
            }
          })
          }
          const responseFailureGoogle=()=>{}
    return(
        <>
                <div id="container">
        <Container fluid> 
  <Row>
      {/* <div id="header">
   <img src="https://th.bing.com/th/id/OIP.T19yiW1DqxTw59kGQgT5AgAAAA?pid=ImgDet&rs=1" alt="" srcset="" />
   <span>DEC</span>
   </div> */}
   <Navbarfn/>
  </Row>
  <Row id="r1">

  </Row>
  <Row>
    <Col xs={4}></Col>
    <Col xs={4}>
    <Form id="form" onSubmit={addLogin}>
        <Form.Label id="userlogin" className="justify-content-center" size="lg"> Login</Form.Label>  
        <br></br>
        <Form.Label  id="smalltext" size="sm" >Enter Username and Password to Login</Form.Label>         
  <Form.Group className="mb-3" controlId="exampleForm.ControlForm.Control1">
    {/* <Form.Label>Email address</Form.Label> */}
    <Form.Control type="email" placeholder="Enter user Email"  name="username" onChange={changeLogin} value={login.username}/>
  </Form.Group>
  <Form.Group className="mb-3" controlId="exampleForm.ControlPassword">
    
   
    <Form.Control type="password" placeholder="Password" name="password" onChange={changeLogin} value={login.password}/>
    <br></br>
    <Button type="submit" variant="danger" className="mb-3" id="button" >SignIn</Button>{' '}
<Link to="/reset">Forget password?</Link>
    <br/>
    <Form.Label  id="smalltext" >OR</Form.Label>  
   <Link to="/Signup"> <Button variant="success" className="mb-3" id="button">Create an Account</Button>{' '}</Link>
   {/* <Button variant="primary" className="mb-3" id="button">Signin with google</Button>{' '} */}
   <GoogleLogin  
    clientId="157798410093-j05aesbiolahsbbv3qp3cmo8fu6rslcq.apps.googleusercontent.com"
    buttonText=" Signin.... With... Google.... "
  
    onSuccess={responseGoogle}
    onFailure={responseFailureGoogle}
    cookiePolicy={'single_host_origin'}
  />
  </Form.Group>
</Form>
    </Col>
    <Col xs={4}></Col>
  </Row>
</Container>
       
        </div>
        </>
    )
}