import Firstpage from "../../Component/FirstPage/FirstPage";
import Signin from "../../Component/Signin/Signin";
import Signup from "../../Component/Signup/Signup";
import {BrowserRouter,Routes,Route} from "react-router-dom"
import AdminHome from "../../Component/Admin Part/AdminHome/AdminHome";
import ViewDoctors from "../../Component/Admin Part/ViewDoctors/ViewDoctors";
import DialogDoctor from "../../Component/Admin Part/DialogueDoctor/DialogueDoctor";
import AddDoctor from "../../Component/Admin Part/addDoctor/addDoctor";
import AddNurse from "../../Component/Admin Part/ViewNurses/addNurse";
import ViewNurses from "../../Component/Admin Part/ViewNurses/ViewNurses";
import EditDoctor from "../../Component/Admin Part/ViewDoctors/editDoctor";
import AddLab from "../../Component/Admin Part/ViewLab/addLab";
import ViewLab from "../../Component/Admin Part/ViewLab/ViewLab";
import ViewPharmacist from "../../Component/Admin Part/ViewPharmacist/ViewPharmacist.";
import AddPharmacist from "../../Component/Admin Part/ViewPharmacist/addPharmacist";
import PatientHome from "../../Component/Patient Part/Patient Home/PatientHome";
import DoctorList from "../../Component/Patient Part/DoctorList/DoctorList";
import EditNurse from "../../Component/Admin Part/ViewNurses/editNurse";
import EditLab from "../../Component/Admin Part/ViewLab/EditLab";
import EditPharmacist from "../../Component/Admin Part/ViewPharmacist/EditPharm";
import DoctorHome from "../../Component/Doctor Part/DoctorHome/DoctorHome";
import Appointmet from "../../Component/Patient Part/Appointment/appointment";
import Appointment from "../../Component/Patient Part/Appointment/appointment";
import ResetPassword from "../../Component/Signin/Reset";
import NewPassword from "../../Component/Signin/NewPassword";
import About from "../../Component/About/About";
import Contact from "../../Component/Contact/contact";
import Services from "../../Component/Services/services";
export default function Home(){
    return(
        <>
{/* <Firstpage/> */}
{/* <Signin/> */}
{/* <Signup/> */}
{/* <AdminHome/> */}
{/* <ViewDoctors/> */}
<BrowserRouter>
<Routes>
    <Route path="/" element={<Firstpage/>}> </Route>
     <Route path="/Signin" element={<Signin/>}></Route>
    <Route path="/Signup" element={<Signup/>}></Route>
    <Route path="/Adminhome" element={<AdminHome/>}></Route>
    <Route path="/Viewdoctors" element={<ViewDoctors/>}></Route>
    <Route path="/Adddoctor" element={<AddDoctor/>}></Route>
    <Route path="/editdr/:id" element={<EditDoctor/>}></Route>
  <Route path="/Viewnurses" element={<ViewNurses/>}></Route>
    <Route path="/Addnurse" element={<AddNurse/>}></Route>
    <Route path="/editnurse/:id" element={<EditNurse/>}></Route>
    <Route path="/Viewlab" element={<ViewLab/>}></Route>
    <Route path="/Addlab" element={<AddLab/>}></Route>
    <Route path="/editlab/:id" element={<EditLab/>}></Route>
    <Route path="/Viewpharm" element={<ViewPharmacist/>}></Route>
    <Route path="/Addpharm" element={<AddPharmacist/>}></Route>
    <Route path="/editpharm/:id" element={<EditPharmacist/>}></Route>
    <Route path="/Patienthome" element={<PatientHome/>}></Route>
    <Route path="/Doctorlist" element={<DoctorList/>}></Route>
    <Route path="/Appointment/:id" element={<Appointment/>}></Route>
    <Route path="/Doctorhome" element={<DoctorHome/>}></Route>
    <Route path="/reset" element={<ResetPassword/>}></Route>
    <Route path="/newpass/:token" element={<NewPassword/>}></Route>
   <Route path="/about" element={<About/>}></Route>
<Route path="/contact" element={<Contact/>}></Route>
<Route path="/services" element={<Services/>}></Route>
    </Routes>
</BrowserRouter>

        </>
    )
}